import KpAvatar from '@/components/kp-avatar/index.vue';
import goDetail from '@/components/uni-compus-components/uniCompus-goDetail.vue';
import { mapState,mapMutations } from 'vuex';
import {host,port} from '@/utils/config.js'
const util = require('util')
export default {
	computed: {
		...mapState(['user','host','port'])
	},
	data() {
		return {
			item: [{ type: 0 }],
			
			userInfoFromWx: {},
			userActivityInfo: {
				username: '天堂屠夫',
				total_visits: 0,
				userActivityNum: {
					num1: 0,
					num2: 0,
					num3: 0,
					num4: 0
				},
				avatar: '../../static/test/waterfull/1.jpg'
			},
			advertisements: ['../../static/test/waterfull/1.jpg', '../../static/test/waterfull/2.jpg', '../../static/test/waterfull/3.jpg']
		};
	},
	components: {
		goDetail,
		KpAvatar
	},
	methods: {
		...mapMutations({
			storeLogin:"storeLogin"
		}),
		
		login(){
			uni.showLoading({
				
			})
			
			wx.login({
				success:(res)=>{
					
					if(res.code){
						uni.request({
							url:util.format('%s:%s/v1/user/token',this.host,this.port),
							method:"POST",
							data:{
								type:100,
								account:res.code,
								
							}
						}).then(res=>{
							
							let item = {
								token:res[1].data.token,
								uid:res[1].data.uid
								
							}
							console.log(item)
							this.storeLogin(item)
							uni.showToast({
								title:"登录成功！"
							})
							uni.hideLoading()
						})
					}
				}
			})
		},
		goConcernDetail() {
			let item = { type: 3 };
			uni.navigateTo({
				url: `/pages/mySave/mySave?item=${encodeURIComponent(JSON.stringify(item))}`
			});
		},
		navtoChangePerson() {
			uni.navigateTo({
				url: '../changePerson/changePerson'
			});
		},
		toDetail() {
			uni.navigateTo({
				url: '/pages/personalDetail/personalDetail'
			});
		}
	},
	async onLoad() {
		
		uni.getUserInfo({
			success:res=>{
				console.log(res)
			}
		})
		
		
	}
};
